          <!-- <b class="win-text" :class="displayProfit(item.symbol, item.direction, item.strike, item.amount, item.optionPrice) !== '0'?'green':'red'">
            {{ displayProfit(item.symbol, item.direction, item.strike, item.amount, item.optionPrice) !== '0' ? language.win : language.lose }}
