                {{language.aoption_lp_amount}}<br>
                <b class="text fs-20">{{ item.amount }}
                      <span>{{ item.direction === 0 ? 'USDT' :  item.symbol }}</span> 
                </b>
              </span>
                 <span class="myTrancheID">
                    <span class="text">ID</span><br>
                     #{{item.trancheID}}
                 </span> 
               <span class="s3 ta-right">
                {{language.aoption_create_time}}<br>#
                <span class="text">{{ item.displayTimestamp }}</span>
              </span>
            </p>
            <footer class="flex jc-between ai-center">
                <p class="flex1">
                  <span class="res-text d-ib text">{{language.aoption_pre_profit}} USDT<br>
                  <b class="gain d-ib text fs-20">{{item.receivePrice.toFixed(6) || 0}} </b>
                  </span>
                </p>
              <p class="block flex jc-between ta-center">
                <span></span>
                <span @click="openBlock(item.createHash)">
