<script>
import MTabs from "@/components/mTabs";
import {mapGetters, mapState,mapActions} from 'vuex';
import {blockConfig, symbolContractMapping} from "@/common/walletConfig";
import MButton from "@/components/mButton";
import { encodeDefxCallName, getDefxCallType } from "@/common/util";
import upImage from '@/images/commons/up.png'
import downImage from '@/images/commons/down.png'
import {coinsTabs} from './datas'

import lookUp from '@/images/option/look-up.png'
import lookDown from '@/images/option/look-down.png'
 const poolTypeTabs = [
  { lable: "up", type: 'up', color: 'green', border: 'border-green hdr-el-green', image: lookUp  },
  { lable: "down", type: 'down', color: 'red', border: 'border-red hdr-el-red', image: lookDown },
]
export default {
  name: "liquidityRecord",
  components: {MButton, MTabs},
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address', 'device','getHistoryTable']),
    ...mapState('option', ['currentRounds', 'contractName']),
    ...mapState('aoption', ['options']),
  },
  watch: {
    currentRounds() {
      this.getList(1);
    },
    $route:{
      //   handler(newVal, oldVal) {
      //       console.log(newVal, oldVal)
      //       if(newVal.query.userId == 1){
                
      //       }else{
                
      //       }
      //  },
      //   deep: true
    },
    getHistoryTable(index){
        if(index == 1){
           clearInterval(this.timer);// 清除定时器
            this.timer = null;  
            this.getList(2);
        }else{
          this.getList(1);
              this.timer = setInterval(() => {
                this.getList(1);
            }, 1000);
        }
    },
    
  },
  data() {
    return {
      upImage,
      downImage,
      tabIndex: 1,
      indexId:1,
      tabs: [
        {type: 0, language_key: 'aoption_holding_lp'},
        {type: 1, language_key: 'aoption_history_lp'},
      ],
      transactionList: [],
      loading: false, //加载中
      openFlag: {},
      roundMap: {
        '5': '5M',
        '15': '15M'
      },
    
    }
  },
  methods: {
      ...mapActions('aoption', ['setOption']),
    getCountdownTime(hedged, timestamp, direction, symbol) {
      let endTime = 0
      const contractName = encodeDefxCallName(getDefxCallType(direction), symbol)
      // 对冲结束时间
      if (hedged) {
        endTime = Number(timestamp)  + Number(this.options[contractName].lockupPeriodForHedgedTranches)
        
     } else { // 非对冲结束时间
        endTime = Number(timestamp)  + Number(this.options[contractName].lockupPeriodForUnhedgedTranches)
      }
      const nowTime = new Date().getTime()
      const countdown = endTime * 1000 - nowTime
      return countdown
    },

    receiveFn(item){
      let contractName = encodeDefxCallName(getDefxCallType(item.direction), item.symbol)
      if(item.hedged){
          let hedgedShare = this.options[contractName].hedgedShare
          let hedgedBalance = this.options[contractName].hedgedBalance
          return (hedgedBalance *  item.share /hedgedShare)/1e18
      }else{
          let unhedgedShare = this.options[contractName].unhedgedShare
          let unhedgedBalance = this.options[contractName].unhedgedBalance
          return (unhedgedBalance *  item.share / unhedgedShare)/1e18
      }
    },
    getList(tabIndex) {
      this.indexId = tabIndex
      if (this.loading) return;
        this.$api.getDefxCallHistoryList(this.address, tabIndex).then(({ tranches }) => {
        this.transactionList = tranches.map(item => {
        const countdownTime = this.getCountdownTime(item.hedged, item.timestamp, item.direction, item.symbol)
        let receivePrice = this.receiveFn(item)
        // const isEnd = !(countdownTime > 0)
        const isEnd  = (countdownTime >= 0);
        let displayCountdown = "";
        if(tabIndex == 1){
          if(countdownTime > 0){
           displayCountdown =  new Date(countdownTime).countdown('dd{day},hh:mm:ss').replace('{day}', this.language.d)
           let str1 =  displayCountdown.split(",")[0]
           let str2 =  displayCountdown.split(",")[1]
           let st = str1.substr(1)
           displayCountdown = st+str2
          }else{
               displayCountdown ="00:00:00"
          }
         }else{
           //  displayCountdown =  new Date(item.endtime-item.timestamp).countdown('dd{day}').replace('{day}', this.language.d)
           //  displayCountdown = displayCountdown ? displayCountdown.split("-")[1]:0
         }
      //   if(isEnd){
      //      displayCountdown =  new Date(countdownTime).countdown('dd{day},hh:mm:ss').replace('{day}', this.language.d)
      //      let str1 =  displayCountdown.split(",")[0]
      //      let str2 =  displayCountdown.split(",")[1]
      //      let st = str1.substr(1)
      //      displayCountdown = st+str2
      // }else{
      //     displayCountdown =  new Date(countdownTime).countdown('dd{day}').replace('{day}', this.language.d)
      //     displayCountdown = displayCountdown ? displayCountdown.split("-")[1]:0
      //   }
      // const displayCountdown = isEnd ? '00:00' : new Date(countdownTime).countdown('dd{day} hh:mm').replace('{day}', this.language.d)
       
        const displayTimestamp = new Date(Number(item.timestamp) * 1000).format('yyyy-MM-dd') 
          // debugger
          return {
            receivePrice,
            ...item,
            displayCountdown,
            isEnd,
            displayTimestamp,
          }
        });
      }).catch((error) => {
        console.error(error)
        this.loading = false;
      })
    },
    tabChangeHandle() {
      this.getList(1);
    },
    claim(roundId, resolution, symbol, $el) {
      $el.setLoading(true);
      let contractName = symbolContractMapping[symbol];
      this.$api.claimReward(roundId, resolution, contractName).then(() => {
        $el.setLoading(false);
        this.$api.getBalance();
        this.deleteRow(roundId);
      }).catch(() => {
        $el.setLoading(false);
      })
    },
    handleWithdraw(trancheID, direction, symbol, btmEl) {
      btmEl.setLoading(true)
      const contractName = encodeDefxCallName(getDefxCallType(direction), symbol)
      this.$api.withdrawDefxCall(contractName, trancheID).then(() => {
        this.$toast.success(this.language.failed_to_enable)
        this.getList(1);
      }).catch((err) => {
        console.log(err)
        if (err?.code !== 4001) {
          this.$toast.error(this.language.take_successfully)
        }
      }).finally(() => {
        btmEl.setLoading(false)
      })
    },
    deleteRow(epoch) {
      let data = this.data;
      let idx = data.findIndex(({round}) => round.epoch === epoch);
      idx !== -1 && data.splice(idx, 1);
    },
    openBlock(hash) {
      window.open(blockConfig.host + hash);
    },
     getActivePoolInfo(contractName) {
      this.$api.getDefxCallData(contractName).then((info) => {
        this.setOption({ key: contractName, info });
      });
    },
  },
  beforeMount() {
    this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[0].type, coinsTabs[0].symbol));
    this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[1].type, coinsTabs[0].symbol));

    if(this.getHistoryTable == 1){
           clearInterval(this.timer);// 清除定时器
            this.timer = null;  
            this.getList(2);
        }else{
            this.getList(1);
            this.timer = setInterval(() => {
              this.getList(1);
           }, 1000);
      }
  },
    beforeUnmount(){
      clearInterval(this.timer);// 清除定时器
      this.timer = null;
   },
     
}
</script>

